<template>
  <div class="jurisdiction">
    <el-row :gutter="20" class="functionalArea">
      <!--<el-col :span="6">-->
      <!--<el-button-group>-->
      <!--<el-button type="primary" icon="el-icon-plus" @click="addFormVisible=true"></el-button>-->
      <!--<el-button type="primary" icon="el-icon-share"></el-button>-->
      <!--<el-button type="primary" icon="el-icon-delete" @click="clearBtnFn"></el-button>-->
      <!--</el-button-group>-->
      <!--</el-col>-->
      <!--<el-col :span="6" :offset="6">-->
      <!--<el-form>-->
      <!--<el-form-item label="主题" :label-width="formLabelWidth">-->
      <!--<el-select v-model="selectThemeId" @change="changeThemeFn" placeholder="请选择主题" style="width:100%;">-->
      <!--<el-option v-for="(item, index) in themeList" :key="index" :label="item.label" :value="item.val"></el-option>-->
      <!--</el-select>-->
      <!--</el-form-item>-->
      <!--</el-form>-->
      <!--</el-col>-->
      <el-col :span="6"  :offset="18">
        <el-input placeholder="请输入内容" v-model="searchVal" clearable>
          <template slot="append">
            <el-button type="success" plain>搜索</el-button>
          </template>
        </el-input>
      </el-col>
    </el-row>
    <div class="jurisdictionTitle">权限树</div>

    <div class="treeWarp">
      <el-tree
              :data="jurisdictionData"
              node-key="id"
              default-expand-all
              :expand-on-click-node="false"
              :render-content="renderContent">
      </el-tree>
    </div>

    <el-dialog title="添加权限" :visible.sync="addFormVisible" width="800px">
      <el-form :model="form">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="权限" :label-width="formLabelWidth">
              <el-input v-model="form.label" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="标识符" :label-width="formLabelWidth">
              <el-input v-model="form.discripe" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="作用" :label-width="formLabelWidth">
              <el-select v-model="form.is_function" placeholder="请选择权限作用" style="width:100%;">
                <el-option v-for="(item, index) in functionList" :key="index" :label="item.label" :value="item.val"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="路由" :label-width="formLabelWidth">
              <el-input v-model="form.routing" placeholder="输入路由" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addSureFn">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="修改权限" :visible.sync="fixFormVisible" width="800px">
      <el-form :model="form1">
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="权限" :label-width="formLabelWidth">
              <el-input v-model="form1.label" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="标识符" :label-width="formLabelWidth">
              <el-input v-model="form1.discripe" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="作用" :label-width="formLabelWidth">
              <el-select v-model="form1.is_function" placeholder="请选择权限作用" style="width:100%;">
                <el-option v-for="(item, index) in functionList" :key="index" :label="item.label" :value="item.val"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="路由" :label-width="formLabelWidth">
              <el-input v-model="form1.routing" placeholder="输入路由" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="fixFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="fixSureFn">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>

<script>
// let id = 1000
let startJurisdiction = {
  'data': [
    {
      'children': [],
      'id': 'root',
      'is_function': 'N',
      'label': 'root',
      'pid': 'root',
      'routing': ''
    }
  ],
  'errmsg': '成功',
  'errno': '0'
}
export default {
  name: 'Jurisdiction',
  data () {
    return {
      jurisdictionData: [],
      searchVal: '',

      formLabelWidth: '120px',
      addFormVisible: false,
      form: {
        discripe: '',
        label: '',
        pid: '',
        is_function: '',
        routing: ''
      },
      fixFormVisible: false,
      form1: {
        discripe: '',
        label: '',
        pid: '',
        is_function: '',
        routing: '',
        id: ''
      },
      functionList: [
        {
          label: '菜单',
          val: 'N'
        },
        {
          label: '功能',
          val: 'Y'
        }
      ],

      curJurisdiction: {}
    }
  },
  watch: {
  },
  created: function () {
    this.getJurisdictionFn()
  },
  methods: {
    append (data) {
      this.addFormVisible = true
      console.log(data)
      this.form.pid = data.pid
      this.curJurisdiction = data
    },
    fixed (node, data) {
      this.fixFormVisible = true
      console.log(1, data)
      this.form1.pid = data.pid
      this.curJurisdiction = data
      this.form1.label = data.label
      this.form1.discripe = data.discripe
      this.form1.is_function = data.is_function
      if (data.routing) {
        this.form1.routing = data.routing
      } else {
        this.form1.routing = ''
      }
      this.form1.id = data.id
    },

    remove (node, data) {
      // let aa = []
      let temp = {
        id_list: [ data.id ]
      }
      this.$confirm('此操作将删除该权限, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.clearFn(temp)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    isDisabled (node, data) {
      console.log(node, data)
      if (node.label === 'root') {
        return true
      } else {
        return false
      }
    },

    renderContent (h, { node, data, store }) {
      if (node.label == 'root') {
        return (
          <span class="custom-tree-node">
            <span>{node.label}</span>
            <span>
              <el-button size="mini" type="success" on-click={ () => this.append(data) } class="rootClass">Append</el-button>
            </span>
          </span>)
      } else {
        return (
          <span class="custom-tree-node">
            <span>{node.label}</span>
            <span>
              <el-button size="mini" type="success" on-click={ () => this.append(data) }>Append</el-button>
              <el-button size="mini" type="warning" on-click={ () => this.fixed(node, data) }>fixed</el-button>
              <el-button size="mini" type="danger" on-click={ () => this.remove(node, data) }>Delete</el-button>
            </span>
          </span>)
      }
    },
    addSureFn () {
      if (this.form.pid === 'root') {
        this.form.pid = null
      } else {
        this.form.pid = this.curJurisdiction.id
      }
      console.log(this.form)
      let temp = {
        discripe: this.form.discripe.replace(/(^\s*)|(\s*$)/g, ''),
        label: this.form.label.replace(/(^\s*)|(\s*$)/g, ''),
        pid: this.form.pid,
        is_function: this.form.is_function,
        routing: this.form.routing
      }
      console.log(temp)
      if (temp.discripe !== '' && temp.label !== '' && temp.is_function !== '') {
        if (temp.is_function === 'N') {
          this.addFn(temp)
        } else {
          if (temp.routing.replace(/(^\s*)|(\s*$)/g, '')) {
            this.addFn(temp)
          } else {
            this.$message({
              message: '路由不能为空',
              type: 'warning'
            })
          }
        }
      } else {
        this.$message({
          type: 'warning',
          message: '请补全信息!'
        })
      }
    },
    fixSureFn () {
      let temp = {
        discripe: this.form1.discripe.replace(/(^\s*)|(\s*$)/g, ''),
        label: this.form1.label.replace(/(^\s*)|(\s*$)/g, ''),
        pid: this.form1.pid,
        is_function: this.form1.is_function,
        routing: this.form1.routing,
        id: this.form1.id
      }
      console.log(temp)
      if (temp.discripe !== '' && temp.label !== '' && temp.is_function !== '') {
        if (temp.is_function === 'N') {
          this.fixFn(temp)
        } else {
          if (temp.routing.replace(/(^\s*)|(\s*$)/g, '')) {
            this.fixFn(temp)
          } else {
            this.$message({
              message: '路由不能为空',
              type: 'warning'
            })
          }
        }
      } else {
        this.$message({
          type: 'warning',
          message: '请补全信息!'
        })
      }
    },
    async addFn (params) {
      try {
        let res = await this.$api.unsplash.postAddJurisdiction(params)
        console.log(res)
        if (res.errno === '0') {
          this.addFormVisible = false
          this.$message({
            type: 'success',
            message: '增加权限成功'
          })
          // 更新权限
          this.getJurisdictionFn()
          // this.getMatterFn(this.selectThemeName);
        } else {
          this.$message({
            type: 'warning',
            message: '增加权限失败'
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async fixFn (params) {
      try {
        let res = await this.$api.unsplash.postfixJurisdiction(params)
        console.log(res)
        if (res.errno === '0') {
          this.fixFormVisible = false
          this.$message({
            type: 'success',
            message: '修改权限成功'
          })
          // 更新权限
          this.getJurisdictionFn()
          // this.getMatterFn(this.selectThemeName);
        } else {
          this.$message({
            type: 'warning',
            message: '修改权限失败'
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async getJurisdictionFn () {
      let params = {}
      try {
        let res = await this.$api.unsplash.getJurisdictions(params)
        if (res.errno === '0') {
          startJurisdiction.data[0].children = res.data.slice()
          this.jurisdictionData = startJurisdiction.data
          sessionStorage.tree = JSON.stringify(this.jurisdictionData)
        } else {
          this.$message({
            type: 'warning',
            message: '获取权限失败'
          })
        }
      } catch (e) {
        console.log(e)
      }
    },
    async clearFn (params) {
      try {
        let res = await this.$api.unsplash.postClearJurisdiction(params)
        console.log(res)
        if (res.errno == '0') {
          // this.fixFormVisible=false
          this.$message({
            type: 'success',
            message: '删除权限成功'
          })
          // 更新权限
          this.getJurisdictionFn()
          // this.getMatterFn(this.selectThemeName);
        } else {
          this.$message({
            type: 'warning',
            message: '删除权限失败'
          })
        }
      } catch (e) {
        console.log(e)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  .jurisdiction{
    .functionalArea{
      height: 80px;
    }
    .custom-tree-node {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 16px;
      padding-right: 8px;
    }
    .rootClass{
      margin-left: -360px;
    }
    .treeWarp{
      min-width: 600px;
      max-width: 800px;
      margin: 0 auto;
      .el-tree-node__content {
        margin-bottom: 10px;
      }
      .el-dialog__body {
        padding: 30px 60px 30px 20px;
      }
    }
    .jurisdictionTitle{
      text-align: left;
      font-size: 20px;
      font-weight: bold;
      padding-bottom: 40px;
    }
  }
</style>



